/**
 * useLoginScreen Hook
 * Shared behavior/logic for LoginScreen across all platforms.
 * File: useLoginScreen.js
 */
import { useCallback, useMemo, useState } from 'react';
import { useRouter } from 'expo-router';
import { useAuth, useI18n, useNetwork } from '@hooks';

const resolveErrorMessage = (t, errorCode) => {
  if (!errorCode) return null;
  const key = `errors.codes.${errorCode}`;
  const resolved = t(key);
  return resolved === key ? t('errors.codes.UNKNOWN_ERROR') : resolved;
};

const useLoginScreen = () => {
  const { t } = useI18n();
  const router = useRouter();
  const { isOffline } = useNetwork();
  const { login, clearError, isLoading, errorCode } = useAuth();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [tenantId, setTenantId] = useState('');

  const errorMessage = useMemo(
    () => resolveErrorMessage(t, errorCode),
    [errorCode, t]
  );

  const canSubmit = useMemo(
    () => Boolean(email && password && tenantId) && !isLoading && !isOffline,
    [email, isLoading, isOffline, password, tenantId]
  );

  const handleSubmit = useCallback(async () => {
    if (!canSubmit) return false;
    clearError();
    await login({
      email: email.trim(),
      password,
      tenant_id: tenantId.trim(),
    });
    return true;
  }, [canSubmit, clearError, email, login, password, tenantId]);

  const handleChangeEmail = useCallback((value) => {
    setEmail(value);
  }, []);

  const handleChangePassword = useCallback((value) => {
    setPassword(value);
  }, []);

  const handleChangeTenantId = useCallback((value) => {
    setTenantId(value);
  }, []);

  const handleGoToRegister = useCallback(() => {
    router.push('/register');
  }, [router]);

  const handleGoToForgotPassword = useCallback(() => {
    router.push('/forgot-password');
  }, [router]);

  return {
    email,
    password,
    tenantId,
    errorMessage,
    isLoading,
    isOffline,
    canSubmit,
    handleSubmit,
    handleChangeEmail,
    handleChangePassword,
    handleChangeTenantId,
    handleGoToRegister,
    handleGoToForgotPassword,
  };
};

export default useLoginScreen;

